*{
  margin: 0;
  padding: 0;
}
body {
  margin: 10px;
  background-color: #e3eeee;;
}
.container{
  max-width: 1400px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  box-sizing: border-box;
  
  
  align-items: center;
}


table {
  display: inline-block;
  border-collapse: collapse;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  /* position: relative; */
 
}
ul{
  margin-left: 20px;
}


code
 {
    background: white;
    padding: 1px 4px 2px 4px;
    font-size: 12px;
    font-weight: bold;
    display: block;
    width: 40%;
  }
/* pre {
  background: white;
  padding: 5px 0 5px 5px;
} */
td,
th {
  border: 2px solid black;
  padding-left: 5px;
  padding-right: 5px;
}

textarea{
  width: 56%;
 min-height:400px;
  
}

.editor{
  width: 100%;
  padding: 10px;
   text-align: center;
  flex: 1;
}

.preview {
  border:1px solid black;
  width: 68%;
  min-height:400px;
  padding: 10px;
  flex: 1;
}
img{
  /* width: 100%; */
}
@media (min-width: 220px)  {
  .container,textarea{
    font-size: 10px;
  }
  code{
    width: 100%;
  }
  img {
    width:100%
  }
  textarea{
    width: 90%;
    min-height: 300px;
  }
  /* smartphones, iPhone, portrait 480x320 phones */ }
@media (min-width:481px)  { 
  .container,textarea{
    font-size: 13px;
  }
    code{
      width: 20em;
    }
  img {
    width:90%
  }
  /* portrait e-readers (Nook/Kindle), smaller tablets @ 600 or @ 640 wide. */ }
@media (min-width:641px)  { 
  img {
    width:80%
  }
  textarea{
    width:68%
  }/* portrait tablets, portrait iPad, landscape e-readers, landscape 800x480 or 854x480 phones */ }
@media (min-width:961px)  {
  img {
    width:70%
  } /* tablet, landscape iPad, lo-res laptops ands desktops */ }
@media (min-width:1025px) { img {
  width:60%
}/* big landscape tablets, laptops, and desktops */ }
@media (min-width:1281px) { 
  img {
    width:50%
  }/* hi-res laptops and desktops */ }
